@mixin highlight() {
  .hljs-comment,
  .hljs-quote {
    color: #b6b18b;
  }

  .hljs-deletion,
  .hljs-name,
  .hljs-regexp,
  .hljs-selector-class,
  .hljs-selector-id,
  .hljs-tag,
  .hljs-template-variable,
  .hljs-variable {
    color: #eb3c54;
  }

  // stylelint-disable selector-class-pattern
  .hljs-built_in,
  .hljs-builtin-name,
  .hljs-link,
  .hljs-literal,
  .hljs-meta,
  .hljs-number,
  .hljs-params,
  .hljs-type {
    color: #e7ce56;
  }
  // stylelint-enable selector-class-pattern

  .hljs-attribute {
    color: #ee7c2b;
  }

  .hljs-addition,
  .hljs-bullet,
  .hljs-string,
  .hljs-symbol {
    color: #4fb4d7;
  }

  .hljs-section,
  .hljs-title {
    color: #78bb65;
  }

  .hljs-keyword,
  .hljs-selector-tag {
    color: #b45ea4;
  }

  .hljs {
    display: block;

    overflow-x: auto;

    padding: 0.5em;

    background: #1c1d21;
    color: #c0c5ce;
  }

  .hljs-emphasis {
    font-style: italic;
  }

  .hljs-strong {
    font-weight: 700;
  }
}
